@import './assets/scss/mixin';
body {
  margin: 0;
  font-family: $font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#parallax-scenes {
  overflow: hidden;

  width: 100%;
  height: 100%;
}

#parallax-scenes div {
  width: 100%;
  height: 100%;
}

#parallax-scenes div:nth-child(1) {
  z-index: 3;
}

#parallax-scenes div:nth-child(2) {
  z-index: 2;
}

#parallax-scenes div:nth-child(3) {
  z-index: 1;
}

#parallax-scenes img:not(.header__bg__img01) {
  width: 110% !important;
  height: 110% !important;

  margin-left: -2%;
  margin-top: -2%;
}